<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h2>Kidz</h2>
      <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
      <i class="fa fa-plus"></i>
      </router-link>
      </div>
      </li>

      <li v-for="kid in kidz" v-bind:key="kid.id" v-bind:class="[{checkedin: kid.in_out}, 'collection-item']">
        <div class="chip">{{kid.age}}</div><span class="name" v-on:click="checkInOut(kid.id, kid.in_out)">{{kid.name}}</span>
        <span class="phone">{{ kid.phone }}</span>
        <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {kid_id: kid.id }}">
          <i class="fa fa-eye fa-2x"></i>
        </router-link>
      </li>
    </ul>
    <div class="fix-action-btn">
    </div>
  </div>
</template>

<script>
import db from './firebaseInit.js';
import firebase from 'firebase'
export default {
  name:'dashboard',
  data(){
    return{
      kidz: [],
    }
  },
  created(){

        db.collection('kidz').orderBy('name').onSnapshot(snap => {
          this.kidz = [];
          snap.forEach(doc => {
        const data = {
          'id': doc.id,
          'name': doc.data().name,
          'in_out': doc.data().in_out,
          'age': doc.data().age,
          'phone': doc.data().phone
        };
        this.kidz.push(data)
      });
    });
  },






  //   db.collection('kidz').orderBy('age').get().then((querySnapshot) => {
  //     querySnapshot.forEach((doc) => {
  //       const data = {
  //         'id': doc.id,
  //         'name': doc.data().name,
  //         'in_out': doc.data().in_out,
  //         'age': doc.data().age
  //       };
  //       this.kidz.push(data);
  //     });
  //   });
  // },
  methods: {
    checkInOut(id, inout) {
      db.collection('kidz').doc(id)
      .update({in_out: !inout})
      .then(docRef => {
        console.log("check in/out complete");
      })

    }
  }
}
</script>

<style scoped>
#dashboard{
  max-width: 600px;
  margin: 0 auto;
}

.checkedin {
  background-color: lightgreen
}

.checkedout {
  background-color: lightcoral
}

.name {
  font-size: 18px;
  font-weight: bold;
  margin-left: 1rem;
}

.phone {
  font-size: 12px;
  font-weight: 500;
  font-style: italic;
  margin: auto;
}

/*li.collection.item{
  padding: 10px 20px !important;
}*/
</style>



